import React, { Component, PropTypes } from 'react';
import Pager from './pager'
// import QuanBatchListItem from './QuanListItem';

export default class QuanList extends Component {
  constructor(props) {
    super(props);
  }



  handleCancelQuan() {

  }

  render () {
    return (
      <div>
        <table className="table table-striped table-bordered table-hover table-full-width dataTable">
            <thead>
              <tr>
                <th></th>
                <th>ID</th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
              </tr>
            </thead>
            <tbody>
            {
              this.props.quanList.map((quan, i) =>
                <tr key={i}>
                  <td>1</td>
                  <td>2</td>
                  <td>3</td>
                  <td>4</td>
                  <td>5</td>
                  <td>6</td>
                  <td>7</td>
                  <td>8</td>
                  <td>9</td>
                  <td>10</td>
                  <td>11</td>
                  <td>12</td>
                  <td>
                    <a href="javascript:void(0)" onClick={this.handleCancelQuan.bind(this)}></a>
                  </td>
                </tr>
              )
            }
            </tbody>
        </table>
      </div>
    );
  }
}

QuanList.propTypes = {
  quanList:PropTypes.array
};


