import React, { Component } from 'react';
import { connect } from 'react-redux';
//import 'react-date-picker/index.css';
import ProductListAddition from './ProductListAddition';

export default class QuanBatchUsageRule extends Component {
	constructor(props) {
		super(props);
	}

	render() {
		let {discountType, setDiscountType, platformLimitList, setPlatformLimitList, applyProductType, setApplyProductType, isBindUser, setIsBindUser
			, expireType, setExpireType, discountAmount, setDiscountAmount, discountPercent, setDiscountPercent, expireDays, setExpireDays, orderAmount
			, setOrderAmount
		} = this.props;
		let isIncludeSpecificPlatform = (platformID => platformLimitList.find((e) => e.platformID === platformID));
		return (
			<div>
				<h1></h1>
				<div>
					<input type="text" value={orderAmount} onChange={(e) => setOrderAmount(e.target.value)} /> 
					<input type="radio" name="discountType" checked={discountType === 2} onChange={() => setDiscountType(0)} />
					<input type="text" value={discountAmount} onChange={(e) => setDiscountAmount(e.target.value)} />
					<input type="radio" name="discountType" checked={discountType === 1} onChange={() => setDiscountType(1)}/>
					<input type="text" value={discountPercent} onChange={(e) => setDiscountPercent(e.target.value)} />
				</div>
				<div>
					
					<input type="checkbox" checked={isIncludeSpecificPlatform(0)} onChange={() => setPlatformLimitList(0)}/>
					<input type="checkbox" checked={isIncludeSpecificPlatform(1)} onChange={() => setPlatformLimitList(1)}/>
					<input type="checkbox" checked={isIncludeSpecificPlatform(2)} onChange={() => setPlatformLimitList(2)}/>PC
				</div>
				<div>
					
					<input type="radio" name="applyProductType" checked={applyProductType === 0} onChange={() => setApplyProductType(0)}/>
					<input type="radio" name="applyProductType" checked={applyProductType === 1} onChange={() => setApplyProductType(1)}/>
					<input type="radio" name="applyProductType" checked={applyProductType === 2} onChange={() => setApplyProductType(2)}/>
				</div>
				<div>
					
					<input type="radio" name="expireType" checked={expireType === 0} onChange={() => setExpireType(0)} />
					
					<input type="radio" name="expireType" checked={expireType === 1} onChange={() => setExpireType(1)}/>
					<input type="text" value={expireDays} onChange={(e) => setExpireDays(e.target.value)} />
				</div>
				<div>
					<input type="checkbox" checked={isBindUser} onChange={setIsBindUser}/>
				</div>
			</div>
		);
	}
}


