import React, { Component, PropTypes } from 'react';

export default class IssueQuan extends Component {
  constructor(props) {
    super(props);
  }

  render () {
  	const {batchId, title} = this.props;
    return (
      <div>
      	<div>ID{batchId}</div>
      	<div>{title}</div>
      </div>
    );
  }
}



