import React, { Component } from 'react';

export default class ChannelDispatchRule extends Component {
	constructor(props) {
		super(props);
	}

	render() {
		const {
			couponQty, setCouponQty, isSale, setIsSale, saleAmount , setSaleAmount
		} = this.props;
		return (
			<div>
				<h1></h1>
				<div>
					
					<input type="text" value={couponQty} onChange={(e) => setCouponQty(e.target.value)} />
				</div>
				<div>
					
					<input type="checkbox" checked={isSale === 1} onChange={e => setIsSale()} />
					
					<input type="text" value={saleAmount} onChange={(e) => setSaleAmount(e.target.value)} />
				</div>
			</div>
		);
	}
}


