import React, { Component } from 'react';

export default class SaleDispatchRule extends Component {
	constructor(props) {
		super(props);
	}

	render() {
		const {
			couponQty, setCouponQty, perUserLimit, setDispatchSaleRulePerUserLimit
		} = this.props;
		return (
			<div>
				<h1></h1>
				<div>
					
					<input type="text" value={couponQty} onChange={(e) => setCouponQty(e.target.value)} />
				</div>
				<div>
					
					<input type="text" value={perUserLimit} onChange={(e) => setDispatchSaleRulePerUserLimit(e.target.value)} />
				</div>
			</div>
		);
	}
}


